import React, {useEffect} from 'react';
import Windows from "./Window";
import Bar from "./Bar";
import {AppRouter} from "./pages/AppRouter";
import {BrowserRouter} from 'react-router-dom';
import FileManager from "./modules/FileManager";
import DataForm from './modules/DataForm';

declare global {
    interface Window {
        cloudinary: any;
        ml: any;
        navigate: any;
    }
}

const pages = {
    'main': "",
    'models': "",
    'orders': "",
    'blueprints': "",
    'shop': " ",
    'gallery': "",
}

function Container({children}: {children: React.ReactElement}) {
    return (
        <div style={{maxWidth: 1200, width: '95%', margin:'0 auto'}}>
            {children}
        </div>
    )
}

function App() {
    return (
        <div className="App">
            <BrowserRouter>
                <Bar tabs={Object.values(pages)} onChange={t => window.navigate(Object.keys(pages)[t])}></Bar>
                <FileManager></FileManager>
                <DataForm></DataForm>
                <Container>
                    <AppRouter></AppRouter>
                </Container>

            </BrowserRouter>

        </div>
    );
}

export default App;
