//@ts-nocheck
import {createSlice} from "@reduxjs/toolkit";
import {Product} from "../my-api-client";

interface IState {
    selected: Product;
}

export const appSlice = createSlice({
    name: "app",
    initialState: {
       selected: {},
    } as IState,
    reducers: {
        setSelected: (state, {payload: selected}) => {
            state.selected = selected;
        },
    }
});

export const { actions, reducer } = appSlice;