import React, {useEffect} from 'react';
import Windows from "../Window";

const FileManager = () => {
    const ref = React.useRef<HTMLDivElement>(null);
    useEffect(() => {
        if (!ref.current) return;

        console.log(ref)
        const config = {
            cloud_name: 'dgkwlszta',
            api_key: '164866611262218',
            username: 'redshock75@gmail.com',
            button_class: 'myBtn',
            button_caption: 'Insert Images',
            inline_container: ref.current,
            remove_header: true,

            default_transformations: [
                [{quality: "auto"}, {fetch_format: "auto"}],
                [{width: 80, height: 80, crop: "fill", gravity: "auto", radius: "max"}, {
                    fetch_format: "auto",
                    quality: "auto"
                }]
            ],
        }
        window.ml = window.cloudinary.createMediaLibrary(config, {
            insertHandler: (d: any) => {
                console.log(d)
            }
        })
        window.ml.show()
    }, [ref.current])

    return (
        <Windows title={''} defaultOpened={false}>
            <div className={'explorer'} ref={ref}></div>
        </Windows>
    );
};

export default FileManager;