import {useForm} from "react-hook-form";
import Typography from "@mui/joy/Typography";
import Box from "@mui/material/Box";
import {Button, capitalize, TextField} from "@mui/material";
import React from "react";

interface Field {
    name: string;
    value: any;
}

export interface IForm {
    caption: string;
    button?: string;
    fields: Field[];
    onSubmit?: (data: any) => any;
    children?: React.ReactElement;
}

export function Form({caption, button="Submit", fields, onSubmit = () => {}, children}: IForm) {
    const {
        register, handleSubmit,
    } = useForm();

    return (
        <>
            <Typography component="h1">
                {caption}
            </Typography>
            <Box component="form" onSubmit={handleSubmit((data) => onSubmit(data))} sx={{mt: 3}}>
                {fields.map(f => <TextField
                    required
                    fullWidth
                    variant="standard"
                    size={'small'}
                    sx={{marginBottom: 2}}
                    {...register(f.name, {value: f.value})}
                    label={capitalize(f.name)}
                    autoFocus
                />)}
                {children}
                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    sx={{mt: 3, mb: 2}}
                >
                    {button}
                </Button>
            </Box>
        </>

    )
}